<template>
  <div class="answer-warp">
    <Header>
      <a class="publish-btn" :class="{'active': inputText.length >= 4 && inputText.length <= 40}" href="javascript:;" slot="right" @click="toPublish">发布</a>
    </Header>
    <GoodsInfo :sku="sku"/>
    <div class="answer-edit">
      <textarea v-model="inputText" placeholder="说出你的问题，已购买的热心用户会帮您解答哦，输入限制在4-40字" maxlength="40"></textarea>
      <span><strong>{{inputText.length}}</strong>/40</span>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header'
import GoodsInfo from './component/goods-info'
import { releaseProductQuestion } from '@/http/service/goods'
// import { mapGetters } from 'vuex'
export default {
  name: 'goodsAnswerAdd',
  components: {
    Header,
    GoodsInfo
  },
  data() {
    return {
      sku: '',
      isActive: false,
      inputText: '',
    }
  },
  activated() {
    this.sku = this.$route.query.sku
  },
  // computed: {
  // ...mapGetters('answerArea', ['getAnswerInfo']),
  // ...mapGetters(['mId']),
  // },
  methods: {
    toPublish() {
      if (this.inputText.length < 4 || this.inputText.length > 40) {
        return;
      }
      releaseProductQuestion({
        sku: this.sku,
        // memberId: this.mId,
        question: this.inputText
      }).then(res => {
        let { success, message } = res;
        if (success) {
          this.$toast('发布成功')
          this.$router.back()
        } else {
          this.$toast(message)
        }
      })
    }
  }
}
</script>

