<template>
  <div class="detail-info" @click="goDetail">
    <img :src="info.defaultImageUrl" alt="">
    <div>
      <h4>{{info.productFullName}}</h4>
      <p>共{{info.questionCount}}个问题</p>
    </div>
  </div>
</template>

<script>
import { getProductQuestionInfo } from '@/http/service/goods'
export default {
  name: 'answerGoodsInfo',
  props: ['sku'],
  data() {
    return {
      info: {},
    }
  },
  watch: {
    sku: {
      handler: function() {
        this.getProductQuestionInfo()
      },
      immediate: true
    }
  },
  methods: {
    getProductQuestionInfo() {
      if (!this.sku) return
      getProductQuestionInfo({
        sku: this.sku
      }).then(res => {
        let { result } = res;
        this.info = result
      })
    },
    goDetail() {
      this.$router.push({
        path: '/goodsDetail',
        query: {
          productId: this.info.productId
        }
      })
    }
  }
}
</script>

